<template>
<div id="app">
  <common-banner :bannerSrc="bannerSrc"></common-banner>
  <common-rule class="btn-rule" @showRuleModal="bindShowRule"></common-rule>
  <common-prize class="my-prize" :path="'nhshb'"></common-prize>
  <common-counter class="chance-box" :chances="chance"></common-counter>
  <hit-egg @hitEgg="hitEgg" :hammerCss="hammerCss"></hit-egg>
  <modal v-if="isShowRule" :showDefaultBtn="true" :btnText="'好的'" :title="'活动规则'" @sure="isShowRule = false" :showClose="true" @close="isShowRule = false">
    <div slot="custom">
      <rules></rules>
    </div>
  </modal>
  <overlay v-if="isShowRule || isShowNochancetip || isShowPrizetip || isShowNomoretip" @clickOverlay="isShowRule = false;isShowPrizetip = false;isShowNomoretip = false;"></overlay>
  <loading v-if="isShowLoading"></loading>
  <common-nochancetip :gameJumpImage="gameJumpImage" :gameJumpUrl="gameJumpUrl" v-if="isShowNochancetip"></common-nochancetip>
  <common-prizetip :prizeLink="prizeLink" :prizeName="prizeName" :prizeImage="prizeImage" v-if="isShowPrizetip"></common-prizetip>
  <common-nomoretip v-if="isShowNomoretip" @bindClose="bindCloseNomoretip"></common-nomoretip>
</div>
</template>

<script>
import commonRule from '@/components/common/commonRule'
import commonPrize from '@/components/common/commonPrize'
import commonCounter from '@/components/common/commonCounter'
import rules from '@/components/zajindan/common/rules'
import modal from '@/components/common/modal'
import overlay from '@/components/common/overlay'
import loading from '@/components/common/loading'
import commonNochancetip from '@/components/common/commonNochancetip'
import commonNomoretip from '@/components/common/commonNomoretip'
import commonPrizetip from '@/components/common/commonPrizetip'
import commonBanner from '@/components/common/commonBanner'
import hitEgg from '@/components/zajindan/common/hitEgg'
import {
  getChance,
  getLuck
} from '@/service/getData'
import { getUrlParams } from '../../../../utils/getUrlParams'

export default {
  name: 'App',
  data() {
    return {
      isShowRule: false,
      isShowOverlay: false,
      isShowLoading: false,
      isShowNochancetip: false,
      isShowNomoretip: false,
      isShowPrizetip: false,
      bgSrc: '',
      gameJumpImage: '',
      gameJumpUrl: '',
      prizeName: '',
      prizeImage: '',
      prizeLink: '',
      bannerSrc: require('../../../../assets/images/games/zajindan/ykhy/banner.png'),
      chance: 0,
      path: 'zjd/ykhy',
      activityKey: 'zajindan_youku',
      channel: 'test',
      hammerCss: {}
    }
  },
  components: {
    commonRule,
    commonPrize,
    commonNochancetip,
    commonNomoretip,
    commonPrizetip,
    commonCounter,
    modal,
    overlay,
    rules,
    loading,
    commonBanner,
    hitEgg
  },
  methods: {
    bindShowRule() {
      this.isShowRule = true
    },
    bindCloseNomoretip() {
      this.isShowNomoretip = false
    },
    async hitEgg(e) {
      let hammerLeft = e.target.offsetParent.offsetLeft * 2 / 100 + 0.9 + 'rem'
      let hammerTop = e.target.offsetParent.offsetTop * 2 / 100 - 0.9 + 'rem'
      this.hammerCss = {
        'left': hammerLeft,
        'top': hammerTop,
        'transition': 'transform .2s linear 0s'
      }
      let res = await getLuck(this.activityKey, this.channel, this.path)
      console.log(res)
      if (res.retCode === '200') {
        this.getChance()
        this.prizeImage = res.prize.imageUrl
        this.prizeName = res.prize.title
        this.prizeLink = res.prize.link
      } else if (res.retCode === '341') {
        if (res.gameJumpImage && res.gameJumpUrl) {
          this.gameJumpImage = res.gameJumpImage
          this.gameJumpUrl = res.gameJumpUrl
          this.isShowNochancetip = true
          setTimeout(() => {
            window.location.href = res.gameJumpUrl
          }, 5000)
        } else {
          this.isShowNomoretip = true
        }
      }
    },
    async getChance() {
      let res = await getChance(this.activityKey, this.channel, 'zjd')
      console.log(res)
      if (res.retCode === '200') {
        this.chance = res.chance
      }
    },
    async initData() {
      this.getChance()
    }
  },
  created() {
    this.initData()
    console.log(getUrlParams('channel'))
  }
}
</script>

<style lang='less'>
@baseUrl: '../../../../assets/styles/games';

@import '@{baseUrl}/common/base.less';
@import '@{baseUrl}/zajindan/common/common.less';
@import '@{baseUrl}/zajindan/ykhy/ykhy.less';
</style>



